/**
 * @file GalleryViewer.tsx
 * @description 图册查看器组件 - 支持密码验证、图片预览、放大缩小、左右切换、下载等功能
 * @author Arran
 * @copyright 2025 Arran ( SuMoChen)
 * @version 1.0.0
 * @created 2025-11-25
 */

'use client';

import { useState, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { motion, AnimatePresence } from 'framer-motion';
import {
  X,
  ChevronLeft,
  ChevronRight,
  ZoomIn,
  ZoomOut,
  Download,
  Lock,
  Eye,
  EyeOff
} from 'lucide-react';
import { getGallery, verifyGalleryPassword } from '@/lib/api';
import { getFileUrl } from '@/lib/utils';

interface GalleryImage {
  id: number;
  filename: string;
  path: string;
  sort_order: number;
  size?: number;
}

interface Gallery {
  id: number;
  title: string;
  description?: string;
  images: GalleryImage[];
  password?: string;
}

interface GalleryViewerProps {
  galleryId: number;
  isOpen: boolean;
  onClose: () => void;
}

export default function GalleryViewer({ galleryId, isOpen, onClose }: GalleryViewerProps) {
  const [gallery, setGallery] = useState<Gallery | null>(null);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [zoom, setZoom] = useState(1);
  const [requirePassword, setRequirePassword] = useState(false);
  const [password, setPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [verifyingPassword, setVerifyingPassword] = useState(false);

  // 触摸滑动相关状态
  const [touchStart, setTouchStart] = useState<number | null>(null);
  const [touchEnd, setTouchEnd] = useState<number | null>(null);

  // 滑动最小距离（像素）
  const minSwipeDistance = 50;

  useEffect(() => {
    if (isOpen && galleryId) {
      loadGallery();
    }
  }, [isOpen, galleryId]);

  useEffect(() => {
    if (isOpen) {
      // 禁止页面滚动
      document.body.style.overflow = 'hidden';
    } else {
      // 恢复页面滚动
      document.body.style.overflow = 'unset';
    }

    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isOpen]);

  // 键盘导航
  useEffect(() => {
    if (!isOpen) return;

    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        onClose();
      } else if (e.key === 'ArrowLeft') {
        handlePrevious();
      } else if (e.key === 'ArrowRight') {
        handleNext();
      } else if (e.key === '+' || e.key === '=') {
        handleZoomIn();
      } else if (e.key === '-') {
        handleZoomOut();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, currentImageIndex, zoom, gallery]);

  const loadGallery = async (pwd?: string) => {
    setLoading(true);
    setError(null);

    try {
      const response = await getGallery(galleryId, pwd);

      if (response.success) {
        setGallery(response.data);
        setRequirePassword(false);
        setPasswordError('');
      } else if (response.requirePassword) {
        setRequirePassword(true);
      } else {
        setError(response.message || '加载图册失败');
      }
    } catch (err: any) {
      if (err.response?.status === 401) {
        if (err.response?.data?.requirePassword) {
          setRequirePassword(true);
        } else {
          // 密码错误，保持密码弹窗打开
          setRequirePassword(true);
          setPasswordError('密码错误');
        }
      } else {
        setError('加载图册失败');
      }
    } finally {
      setLoading(false);
    }
  };

  const handlePasswordSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!password.trim()) {
      setPasswordError('请输入密码');
      return;
    }

    setVerifyingPassword(true);
    setPasswordError('');

    try {
      await loadGallery(password);
    } catch (err) {
      // Error handled in loadGallery
    } finally {
      setVerifyingPassword(false);
    }
  };

  const handleNext = () => {
    if (!gallery || gallery.images.length === 0) return;
    setCurrentImageIndex((prev) => (prev + 1) % gallery.images.length);
    setZoom(1); // 重置缩放
  };

  const handlePrevious = () => {
    if (!gallery || gallery.images.length === 0) return;
    setCurrentImageIndex((prev) => (prev - 1 + gallery.images.length) % gallery.images.length);
    setZoom(1); // 重置缩放
  };

  const handleZoomIn = () => {
    setZoom((prev) => Math.min(prev + 0.25, 3));
  };

  const handleZoomOut = () => {
    setZoom((prev) => Math.max(prev - 0.25, 0.5));
  };

  const handleDownloadCurrent = () => {
    if (!gallery || gallery.images.length === 0) return;

    const currentImage = gallery.images[currentImageIndex];
    const imageUrl = getFileUrl(currentImage.path);

    // 创建临时链接并触发下载
    const link = document.createElement('a');
    link.href = imageUrl;
    link.download = currentImage.filename;
    link.target = '_blank';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const handleDownloadAll = async () => {
    if (!gallery || gallery.images.length === 0) return;

    // 逐个下载图片
    for (let i = 0; i < gallery.images.length; i++) {
      const image = gallery.images[i];
      const imageUrl = getFileUrl(image.path);

      // 延迟下载，避免浏览器阻止
      await new Promise((resolve) => setTimeout(resolve, i * 500));

      const link = document.createElement('a');
      link.href = imageUrl;
      link.download = image.filename;
      link.target = '_blank';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  const handleClose = () => {
    setZoom(1);
    setCurrentImageIndex(0);
    setGallery(null);
    setRequirePassword(false);
    setPassword('');
    setPasswordError('');
    onClose();
  };

  // 处理触摸开始
  const onTouchStart = (e: React.TouchEvent) => {
    setTouchEnd(null);
    setTouchStart(e.targetTouches[0].clientX);
  };

  // 处理触摸移动
  const onTouchMove = (e: React.TouchEvent) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  // 处理触摸结束
  const onTouchEnd = () => {
    if (!touchStart || !touchEnd) return;

    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe) {
      handleNext(); // 向左滑动，显示下一张
    } else if (isRightSwipe) {
      handlePrevious(); // 向右滑动，显示上一张
    }
  };

  if (!isOpen) return null;

  const modalContent = (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 z-[99999] flex items-center justify-center bg-black/90 backdrop-blur-sm"
        onClick={handleClose}
      >
        <div
          className="relative w-full h-full flex flex-col"
          onClick={(e) => e.stopPropagation()}
          onTouchStart={onTouchStart}
          onTouchMove={onTouchMove}
          onTouchEnd={onTouchEnd}
        >
          {/* 顶部工具栏 - 精简版 */}
          <div className="absolute top-0 left-0 right-0 z-10 bg-gradient-to-b from-black/80 to-transparent p-4">
            <div className="flex items-center justify-between max-w-7xl mx-auto">
              {/* 左侧占位 */}
              <div className="w-10"></div>

              {/* 中间：图片计数 */}
              <div className="flex-1 flex justify-center">
                {!loading && !requirePassword && gallery && gallery.images.length > 1 && (
                  <div className="bg-black/50 backdrop-blur-sm rounded-full px-4 py-2">
                    <span className="text-white text-sm font-medium">
                      {currentImageIndex + 1} / {gallery.images.length}
                    </span>
                  </div>
                )}
              </div>

              {/* 右侧：下载和关闭按钮 */}
              <div className="flex items-center gap-2">
                {!loading && !requirePassword && gallery && gallery.images.length > 0 && (
                  <button
                    onClick={handleDownloadCurrent}
                    className="p-2 bg-white/10 hover:bg-white/20 rounded-lg transition-colors"
                    title="下载当前图片"
                  >
                    <Download className="w-5 h-5 text-white" />
                  </button>
                )}

                {!requirePassword && (
                  <button
                    onClick={handleClose}
                    className="p-2 bg-white/10 hover:bg-white/20 rounded-lg transition-colors"
                    title="关闭"
                  >
                    <X className="w-5 h-5 text-white" />
                  </button>
                )}
              </div>
            </div>
          </div>

          {/* 中间内容区域 - 全局垂直居中 */}
          <div className="flex-1 flex flex-col items-center justify-center">
            {loading && (
              <div className="text-center">
                <div className="inline-block animate-spin rounded-full h-12 w-12 border-4 border-white border-t-transparent mb-4"></div>
                <p className="text-white">加载中...</p>
              </div>
            )}

            {error && (
              <div className="text-center">
                <p className="text-red-400 text-lg mb-4">{error}</p>
                <button
                  onClick={handleClose}
                  className="px-6 py-2 bg-white/10 hover:bg-white/20 text-white rounded-lg transition-colors"
                >
                  关闭
                </button>
              </div>
            )}

            {requirePassword && (
              <motion.div
                initial={{ scale: 0.95, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ duration: 0.2 }}
                className="w-full max-w-sm bg-bg-secondary rounded-xl p-5 shadow-xl border border-border-primary relative"
              >
                {/* 右上角关闭按钮 */}
                <button
                  onClick={handleClose}
                  className="absolute top-4 right-4 p-1.5 text-text-tertiary hover:text-text-primary hover:bg-bg-tertiary rounded-lg transition-colors"
                  title="关闭"
                  disabled={verifyingPassword}
                >
                  <X className="w-5 h-5" />
                </button>

                <div className="text-center mb-5">
                  <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-3">
                    <Lock className="w-6 h-6 text-primary-600" />
                  </div>
                  <h3 className="text-base font-bold text-text-primary mb-1">
                    需要密码访问
                  </h3>
                  <p className="text-xs text-text-tertiary">
                    请输入密码查看图册内容
                  </p>
                </div>

                <form onSubmit={handlePasswordSubmit} className="space-y-3">
                  <div>
                    <div className="relative">
                      <input
                        type={showPassword ? 'text' : 'password'}
                        name="gallery-access-password"
                        value={password}
                        onChange={(e) => {
                          setPassword(e.target.value);
                          setPasswordError('');
                        }}
                        placeholder="请输入密码"
                        className={`w-full px-3 py-2.5 pr-10 text-sm border ${
                          passwordError ? 'border-red-500' : 'border-border-primary'
                        } rounded-lg bg-bg-tertiary text-text-primary focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent`}
                        autoFocus
                        autoComplete="off"
                        disabled={verifyingPassword}
                      />
                      <button
                        type="button"
                        onClick={() => setShowPassword(!showPassword)}
                        className="absolute right-3 top-1/2 -translate-y-1/2 text-text-tertiary hover:text-text-primary transition-colors"
                      >
                        {showPassword ? (
                          <EyeOff className="w-4 h-4" />
                        ) : (
                          <Eye className="w-4 h-4" />
                        )}
                      </button>
                    </div>
                    {passwordError && (
                      <p className="mt-1.5 text-xs text-red-500">{passwordError}</p>
                    )}
                  </div>

                  <button
                    type="submit"
                    className="w-full px-3 py-2 text-sm bg-primary-500 text-white rounded-lg hover:bg-primary-600 transition-colors disabled:opacity-50"
                    disabled={verifyingPassword || !password}
                  >
                    {verifyingPassword ? '验证中...' : '确认'}
                  </button>
                </form>
              </motion.div>
            )}

            {!loading && !error && !requirePassword && gallery && gallery.images.length > 0 && (
              <>
                {/* 上方占位 - 与顶部X/X的间距 */}
                <div className="h-20"></div>

                {/* 图片容器 */}
                <div className="relative flex-shrink-0 px-4 lg:px-24">
                  {/* 图片显示 - 限制最大高度 */}
                  <motion.img
                    key={currentImageIndex}
                    src={getFileUrl(gallery.images[currentImageIndex].path)}
                    alt={gallery.images[currentImageIndex].filename}
                    className="max-w-full object-contain mx-auto"
                    style={{
                      transform: `scale(${zoom})`,
                      transition: 'transform 0.3s ease-out',
                      maxHeight: 'calc(100vh - 320px)', // 顶部80px + 上间距80px + 下间距80px + 底部80px
                      maxWidth: '100%'
                    }}
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                  />

                  {/* 左右切换按钮 - 桌面端在图片外，移动端在图片内 */}
                  {gallery.images.length > 1 && (
                    <>
                      <button
                        onClick={handlePrevious}
                        className="absolute left-2 lg:-left-16 top-1/2 -translate-y-1/2 p-3 bg-black/20 hover:bg-black/40 rounded-full transition-colors backdrop-blur-sm"
                        title="上一张 (←)"
                      >
                        <ChevronLeft className="w-6 h-6 text-white/60" />
                      </button>

                      <button
                        onClick={handleNext}
                        className="absolute right-2 lg:-right-16 top-1/2 -translate-y-1/2 p-3 bg-black/20 hover:bg-black/40 rounded-full transition-colors backdrop-blur-sm"
                        title="下一张 (→)"
                      >
                        <ChevronRight className="w-6 h-6 text-white/60" />
                      </button>
                    </>
                  )}
                </div>

                {/* 下方占位 - 与底部放大缩小按钮的间距 */}
                <div className="h-20"></div>
              </>
            )}

            {!loading && !error && !requirePassword && gallery && gallery.images.length === 0 && (
              <div className="text-center text-white">
                <p className="text-lg mb-4">该图册暂无图片</p>
                <button
                  onClick={handleClose}
                  className="px-6 py-2 bg-white/10 hover:bg-white/20 rounded-lg transition-colors"
                >
                  关闭
                </button>
              </div>
            )}
          </div>

          {/* 底部区域：放大缩小控制器 + 缩略图 */}
          {!loading && !error && !requirePassword && gallery && gallery.images.length > 0 && (
            <div className="absolute bottom-0 left-0 right-0 z-10">
              {/* 放大缩小按钮 - 缩小15% */}
              <div className="flex justify-center pb-3">
                <div className="flex items-center gap-1.5 bg-black/50 backdrop-blur-sm rounded-full px-3 py-1.5">
                  <button
                    onClick={handleZoomOut}
                    className="p-1 hover:bg-white/10 rounded-full transition-colors"
                    title="缩小"
                  >
                    <ZoomOut className="w-4 h-4 text-white" />
                  </button>

                  <span className="text-white text-xs min-w-[50px] text-center font-medium">
                    {Math.round(zoom * 100)}%
                  </span>

                  <button
                    onClick={handleZoomIn}
                    className="p-1 hover:bg-white/10 rounded-full transition-colors"
                    title="放大"
                  >
                    <ZoomIn className="w-4 h-4 text-white" />
                  </button>
                </div>
              </div>

              {/* 底部缩略图 */}
              {gallery.images.length > 1 && (
                <div className="bg-gradient-to-t from-black/80 to-transparent p-4">
                  <div className="flex justify-center max-w-7xl mx-auto">
                    <div className="flex gap-2 overflow-x-auto scrollbar-thin scrollbar-thumb-white/20 scrollbar-track-transparent">
                      {gallery.images.map((image, index) => (
                        <button
                          key={image.id}
                          onClick={() => {
                            setCurrentImageIndex(index);
                            setZoom(1);
                          }}
                          className={`flex-shrink-0 w-16 h-16 rounded-lg overflow-hidden border-2 transition-all ${
                            index === currentImageIndex
                              ? 'border-primary-500 opacity-100'
                              : 'border-white/20 opacity-50 hover:opacity-75'
                          }`}
                        >
                          <img
                            src={getFileUrl(image.path)}
                            alt={image.filename}
                            className="w-full h-full object-cover"
                          />
                        </button>
                      ))}
                    </div>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </motion.div>
    </AnimatePresence>
  );

  // 使用 Portal 渲染到 body，避免被父容器的 z-index 限制
  if (typeof window !== 'undefined') {
    return createPortal(modalContent, document.body);
  }

  return null;
}
